.blocks_comp {
  width: 100%;
  margin: 100px 0 0 0;
}

.blocks_comp .width {
  margin: 0 auto;
  max-width: 1140px;
  width: 100%;
}

.blocks_comp .home_block {
  width: 100%;
}

.blocks_comp .block {
  max-width: 250px;
}

.i {
  display: flex !important;
}
.blocks_comp .box_block_block {
  margin: 0 auto;
  width: 95%;
  border-radius: 12px;
  height: 160px;
  border: 1px solid #303241;
  position: relative;
  padding: 20px 0 0 0;
  background: #1A1B23;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.box_block_block .absolune {
  width: 50%;
  display: flex;
  flex-direction: column;
}

.blocks_comp .margen {
  width: 50%;
  height: 130px;
  display: flex;
  align-items: center;
}

.diference {
  display: flex;
  gap: 5px;
  margin: 0 auto;
}
.logo-name {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}
.blocks_comp p {
  color: #fff;
  font-size: 17px;
  font-weight: 400;
  margin-bottom: 8px;
}

.blocks_comp h1 {
  display: flex;
  flex-direction: row;
  color: #fff;
  font-size: 22px;
  font-weight: 600;
  gap: 2px;
  margin: 0 auto;
}

.blocks_comp .dollar {
  color: #303241;
}

.blocks_comp .logo_img {
  width: 28px;
  height: 28px;
  margin-right: 7px;
  border-radius: 50%;
}

.line_chart_one {
  position: absolute;
}

@media (max-width: 800px) {
  .blocks_comp .width {
    max-width: 800px;
    width: 100%;
    margin-top: 100px;
  }
  .blocks_comp{
    width: 100%;
    margin:30px 0 0 0;
  }
}
