.header {
  width: 100%;
  z-index: 99;
  border-bottom: 1px solid #4f4b5c;
}

.header .container {
  max-width: 1140px;
  margin: 0 auto;
  width: 90%;
}

.header .login {
  padding: 10px 25px 10px 25px;
  border: 1px solid #8d6ae2;
  border-radius: 8px;
  background: transparent;
  font-size: 14px;
  font-weight: 700;
  color: #fcfcfc !important;
  cursor: pointer;
}

.header .container {
  margin: 0 auto;
  display: flex;
  align-items: center;
  border-radius: 5px;
  padding: 20px 0;
  width: 93%;
}

.header a {
  color: #fff;
  font-size: 18px;
  font-weight: 600;
  text-decoration: none;
}

.header .pages {
  margin-left: 5.8%;
  display: flex;
  align-items: center;
  gap: 36px;
}

.header .pages a {
  color: #fff;
  font-size: 18px;
  font-weight: 500;
  line-height: 1;
  position: relative;
  transition: 0.2s ease;
  padding: 8px 0;
}

.header .pages .page.active {
  color: var(--blue);
}

.header .pages a:hover::before {
  transform: scaleX(1);
  transform-origin: center;
  transition: transform 0.3s;
}

.header .pages a::before {
  content: "";
  width: 100%;
  height: 1px;
  background-color: #ffffff;
  position: absolute;
  left: 0;
  bottom: 0;
  transform: scaleX(0);
  transform-origin: center;
  transition: transform 0.5s;
}

.header .color {
  display: flex;
  align-items: center;
}

/* .header .color:hover {
  opacity: 0.8;
} */

.header .battons {
  margin-left: auto;
  display: flex;
  align-items: center;
  gap: 20px;
}

.header .btn {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 14px;
  font-weight: 700;
  line-height: 1;
  border-radius: 10px;
  padding: 14px 25px !important;
  background-color: #8D6AE2 !important;
  cursor: pointer;
}

.header .btn:hover {
  opacity: 0.8;
}

.header .btn.dashboard {
  width: 80%;
}

.header #toggle,
.header .hamburger,
.header .nav,
.header .nav_wrapper {
  display: none;
}

.header .hamburger {
  width: 24px;
  height: 24px;
  z-index: 5;
  cursor: pointer;
}

.header .hamburger div {
  position: relative;
  width: 30px;
  height: 4px;
  border-radius: 3px;
  transition: all 0.3s ease-in-out;
  background: #fff;
  margin-top: 6px;
}

.header .hamburger div:first-child {
  margin: 0;
}

.header #toggle:checked+.hamburger .top-bun {
  transform: rotate(-45deg);
  margin-top: 10px;
}

.header #toggle:checked+.hamburger .bottom-bun {
  opacity: 0;
  transform: rotate(45deg);
}

.header #toggle:checked+.hamburger .meat {
  transform: rotate(45deg);
  margin-top: -4px;
}

.header .nav {
  margin-top: -110px;
  width: 100%;
  height: 120vh;
  background: #171822;
  transition: all 0.3s ease-in-out;
  padding-top: 140px;
  z-index: 1;
  box-shadow: 5px 10px 15px #000;
}

.header .nav_wrapper {
  margin: 0 auto;
  width: 90%;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  animation: fixed_modal_menu 0.7s ease;
}

.logo-header {
  height: 25px;
}

@keyframes fixed_modal_menu {
  0% {
    opacity: 0;
    right: -200px;
  }

  100% {
    opacity: 1;
    right: 0;
  }
}

.header .nav_wrapper .login {
  margin: 10px;
  text-align: center;
}

.header .nav_wrapper .btn {
  width: 100%;
  margin-top: 25px;
}

.header .nav_wrapper .page {
  margin: 10px;
  transition: 0.3s ease;
}

.header .login,
.header .page {
  color: #8d8d8d;
}

.header .login.active,
.header .page.active {
  color: #fff;
}

@media (max-width: 900px) {
  .header .nav_wrapper .page:hover {
    color: #fff;
  }

  .header .battons {
    display: none;
  }

  .header .pages {
    display: none;
  }

  .header .container {
    display: flex;
    justify-content: space-between;
  }

  .header .hamburger,
  .header .nav {
    display: block;
  }

  .header .nav_wrapper .btn {
    margin: 10px;
    text-align: center !important;
  }
}