.OurAdvantages {
  margin-top: 163px;
}

.OurAdvantages h2 {
  color: #fff;
}

.OurAdvantages label {
  display: table;
  width: 80%;
  color: #898ca9;
  font-weight: 400;
  font-size: 16px;
  line-height: .95;
  text-align: center;
  margin: 10px auto 30px auto;
}

.lok {
  width: 90px;
  height: 90px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #171822;
  margin-bottom: 10px;
}


.advantage-wrapper {
  margin-top: 20px;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.advantage_wrar {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(210px, 1fr));
  gap: 20px;
}