.meet {
  width: 100%;
}
.meet .container {
  margin: 0 auto;
  width: 90%;
  height: 100%;
  padding-top: 100px;
}
.meet .save1 {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.meet .image {
  width: 350px
}
.meet .wrapper h1 {
  color: #fff;
  font-size: 42px;
  font-weight: 700;
  margin-bottom: 10px;
  line-height: 1;
}
.meet .wrapper p {
  color: #fff;
  font-size: 14px;
  font-weight: 400;
  width: 40%;
}
.meet .wrapper .spa{
  font-size: 14px;
  font-weight: 400;
  color: #898CA9;
}
.meet .save2 {
  margin: 0 auto;
  margin-top: 85px;
  width: 100%;
}
.meet .save2 h1 {
  color: #dddee8;
  font-size: 40px;
  font-weight: 700;
  margin-bottom: 10px;
}
.meet .save2 p {
  margin-top: 10px;
  color: #fff;
  font-size: 19px;
  font-weight: 400;
}
.meet .loke{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: url("./../../img/cor.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.meet .abouts label{
  color: #fff;
  margin-top: 14px;
  font-weight: 300;
}
.meet .abouts{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.meet .clear{
  margin-top: 70px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(210px, 1fr));
  gap: 20px;
  align-items: center;
  text-align: center;
}
.about-image-block {
  display: flex;
  justify-content: center;
}

@media (max-width: 640px) {
  .meet .wrapper p {
    width: 100%;
    text-align: center;
  }
  .meet .save1  {
    display: flex;
    flex-direction: column-reverse;
    text-align: center;
  }
  .wrapper{
    width: 100%;
    margin-top: 24px;
  }
  .meet .container {
    padding-top: 120px;
  }
  .meet .image {
    margin-top: 30px;
  }
  .meet .wrapper h1 {
    font-size: 20px;
    text-align: center;
  }
  .meet .image {
    width: 280px;
  }
  .meet .save2 h1 {
    font-size: 22px;
  }
  .meet .save2 p {
    font-size: 15px;
  }
}
