#login {
    width: 100%;
    min-height: 260px;
    background: url("../../img/Eclipse.svg") no-repeat center / cover;
    padding-bottom: 100px;
}

#login .login__flex {
    display: flex;
    align-items: center;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    padding: 30px 0;
}

#login .bg_login {
    width: 100%;
}

#login .container {
    width: 1140px;
    max-width: 90%;
    margin: 0 auto;
    padding-top: 100px;
}

#login .login_all {
    display: flex;
    flex-direction: column;
}

#login .login_all h1 {
    color: var(--white, #fff);
    font-size: 30px;
    font-style: normal;
    font-weight: 600;
    line-height: 40px;
    margin: 0 0 10px 0;
}

#login .login_all h3 {
    color: var(--white, #fff);
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

#login .login_all .login_span a {
    color: #30e0a1;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

#login .login_from {
    display: flex;
    flex-direction: column;
}

#login .login_from label {
    color: var(--white, #fff);
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    margin: 20px 0 10px 0;
}

#login .login_from input {
    width: 100%;
    height: 50px;
    padding: 20px;
    border-radius: 10px;
    outline: none;
    border: 1px solid var(--unnamed, #898CA9);
    background: var(--unnamed, #000);
    color: #fff;
    font-size: 14px;
    font-weight: 300;
}

#login .login_from input::placeholder {
    color: var(--unnamed, #9e9ea5);
}

#login .login_from input:hover {
    border: 1px solid #fff;
}

#login .forgot_your_password {
    margin: auto;
    color: #4ac49e;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: 140%;
    text-decoration: none;
}

#login .login_from button {
    cursor: pointer;
    width: 25%;
    border-radius: 12px;
    padding: 15px;
    background: #251F33 !important;
    border: none;
    color: var(--white, #fff);
    font-size: 16px;
    font-weight: 700;
    line-height: 1;
    margin: 20px auto;
}

#login .login_from button:hover {
    opacity: 0.8;
}

#login .input_box {
    position: relative;
}

#login .span-icon {
    top: 15px;
    right: 20px;
    font-size: 20px;
    color: white;
    position: absolute;
    z-index: 1;
    cursor: pointer;
}

@media (max-width: 950px) {
    #login .container {
        padding-top: 130px;
    }
}

@media (max-width: 800px) {
    #login .bg_login {
        width: 60%;
    }

    #login .login_from label {
        text-align: start !important;
    }

    #login .container {
        padding-top: 50px;
        text-align: center;
    }

    #login .login_all {
        display: flex;
        flex-direction: column;
        width: 100%;
    }

    #login .login__flex {
        display: flex;
        flex-direction: column-reverse;
    }
}

@media (max-width: 520px) {
    #login .from input {
        width: 380px;
        height: 43px;
    }
}

@media (max-width: 480px) {
    #login .from input {
        width: 300px;
        height: 45px;
    }
}

@media (max-width: 410px) {
    #login .from input {
        width: 270px;
        height: 43px;
    }
}