.home .container {
  margin: 0 auto;
  max-width: 1140px;
  width: 90%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.home .container_block {
  max-width: 1140px;
  margin: 0 auto;
  width: 90%;
}

.home .wrapper {
  width: 51%;
}

.home p {
  font-size: 24px;
  font-weight: 300;
  color: #fff;
  margin-bottom: 15px;
  line-height: 1;
  margin-top: 10px;
}

.home h4 {
  font-size: 18px;
  font-weight: 600;
  color: #898CA9;
}

.Transparent {
  font-family: Nunito !important;
  color: #898CA9 !important;
  font-size: 18px !important;
  font-weight: 500 !important;
  line-height: 1 !important;
}

.home .wrapper2 p {
  font-size: 14px;
  font-weight: 300;
}

.home .first {
  color: var(--green);
}

.home .btn {
  outline: none;
  border: none;
  padding: 15px 35px;
  background: var(--green);
  border-radius: 5px;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
}

.home .btn:hover {
  opacity: 0.8;
}

.home .wrapper2 {
  width: 285px;
  height: 153px;
  display: grid;
  grid-template-rows: 1fr 1fr;
}

.home .save {
  display: flex;
  align-items: center;
}

.home .save .bold {
  font-size: 20px;
}

.home .box {
  margin-left: 10px;
  padding: 5px 0;
}

.home-button-wrap {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 20px;
  margin: 40px 0 80px 0;
}

.home-button-wrap h5 {
  font-size: 14px;
  font-weight: 700;
  color: #fff;
  line-height: 1.5;
}

.home .modal-window .modal-text h1 {
  font-weight: 700 !important;
}

.home .modal-window .modal-text p {
  color: rgba(255, 255, 255, .7) !important;
  font-weight: 400;
  line-height: 1;
  text-align: center;
}

.home .modal-window .modal-text {
  padding: 0;
  overflow: hidden;
}

.home .modal-window .modal-text img {
  max-width: 100%;
  max-height: 100%;
}

.home .modal-mobile-app {
  width: 100%;
  height: 300px;
  background: url('../../img/mobile-app.svg') no-repeat center top;
  background-size: cover;
}

.home .modal-text-wrap .app-links {
  margin-top: 20px;
  max-width: 100%;
  flex-direction: row;
}

.home .modal-text-wrap .app-links a {
  width: 50%;
  padding: 10px;
  gap: 10px;
}

.home .modal-window .modal-text-wrap {
  padding: 20px;
}

.home .app-links a div {
  gap: 0;
}

.home .qr-code-soon {
  position: absolute;
  display: block;
  width: 60px;
  height: 25px;
  top: 0;
  right: 10px;
  bottom: 0;
  margin: auto;
  text-align: center;
}

.calas img {
  object-fit: contain;
  object-position: center;
}

.home-button-wrap img {
  max-width: 120px;
}

@media (max-width: 750px) {
  .calas::after {
    display: none;
  }

  .home {
    width: 100%;
    margin-bottom: 80px;
    background-image: url('../../img/logoMobile.svg');
    background: transparent;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 50% 57%;
  }

  .home .container {
    padding-top: 50px;
    width: 90%;
    display: flex;
    justify-content: flex-start;
    display: grid;
    grid-template-columns: 1fr;
  }

  .home-button-wrap {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 20px;
    margin: 40px 0 20px 0;
  }

  .home .wrapper {
    width: 60%;
    text-align: center;
  }

  .home .btn {
    width: 100%;
    font-size: 14px;
  }
}

@media (max-width: 600px) {

  .home h1 {
    font-size: 30px;
    font-weight: 700;
  }

  .home p {
    font-size: 16px;
  }

  .home .wrapper {
    width: 100%;
  }

  .home .wrapper2 {
    width: 100%;
  }

  .home .btn {
    width: 100%;
    font-size: 14px;
  }
}