#stay .container {
  width: 1140px;
  max-width: 90%;
  margin: 0 auto;
}

.scroll {
  gap: 20px;
  display: flex;
  width: 70%;
  overflow-x: auto;
}

.btn-see {
  border: none;
  color: #fff;
  font-size: 14px;
  font-weight: 700;
  line-height: 1;
  border-radius: 10px;
  padding: 12px 30px;
  background-color: #8d6ae2 !important;
}

.skey_h1 {
  color: #fff;
  font-size: 30px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  text-align: center;
  margin: 100px 0 40px 0;
}

#stay .width {
  width: 350px;
}

.news-all {
  padding: 50px 0 50px 30px;
  border: 1px solid #8d6ae2;
  display: flex;
  border-radius: 25px;
  gap: 20px;
  align-items: center;
}

.news-all h2 {
  color: #fff;
  font-size: 30px;
  font-weight: 700;
}

.news-all button {
  border: none;
  margin-top: 15px;
  color: #fff;
  font-size: 14px;
  font-weight: 700;
  line-height: 1;
  border-radius: 10px;
  padding: 12px 30px;
  background-color: #8d6ae2 !important;
}

.news-all p {
  font-size: 16px;
  font-weight: 200;
  color: #fcfcfc;
  margin-top: 15px !important;
  line-height: 1;
}

#stay .skey_block {
  height: 340px;
  width: 260px;
  margin: 0 auto;
  min-height: 255px;
  border-radius: 20px;
  background: var(--unnamed, #171822);
  margin-bottom: 20px;
}

.context_box-item {
  align-items: center;
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.skey_block h4 {
  font-size: 14px;
  font-weight: 400;
  line-height: 1;
}

#stay .skey_block p {
  color: #777;
  font-size: 14px;
  font-weight: 500;
}

#stay .news-photo {
  width: 100%;
  height: 200px;
  overflow: hidden;
  position: relative;
  border-radius: 10px;
}

#stay .news-photo img {
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  object-fit: cover;
  object-position: center;
}

.skey_block h4 {
  margin-top: 12px;
  color: #fff;
  font-style: normal;
  overflow: hidden;

}

#stay .not_data {
  width: 100%;
  height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
}

#stay .not_data {
  font-size: 25px;
  font-weight: 600;
}

#stay .slick-prev:before,
#stay .slick-next:before {
  display: none;
}

.slick-dots li:nth-child(1) button:before {
  width: 60px;
  height: 3px;
  border-radius: 5px;
  background: #fff;
  margin-left: -90px;
  font-size: 0;
}

.slick-dots li:nth-child(2) button:before {
  width: 60px;
  height: 3px;
  border-radius: 5px;
  background: #fff;
  margin-left: -35px;
  font-size: 0;
}

.slick-dots li:nth-child(3) button:before {
  width: 60px;
  height: 3px;
  border-radius: 5px;
  background: #fff;
  margin-left: 20px;
  font-size: 0;
}

.skey_btn_block {
  width: 100%;
  display: flex;
  justify-content: center;
  margin: -20px 30px 0 0;
  position: relative;
}

.skey_btn {
  border-radius: 4px;
  padding: 15px 50px;
  border: none;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  line-height: 1;
}

.skey_btn:hover {
  opacity: 0.8;
}

@media(max-width: 768px) {
  .news_foto {
    width: 100% !important;
    text-align: center;
    padding: 0 20px 0px 0;
  }

  .scroll {
    width: 100%;
  }

  .news-all {
    padding: 50px 0 50px 30px;
    border: 1px solid #8d6ae2;
    display: flex;
    border-radius: 25px;
    gap: 20px;
    align-items: center;
    flex-direction: column;
    width: 100%;
  }
}

@media (max-width: 414px) {
  .skey_btn_block {
    position: relative;
  }

  .slick-dots {
    position: absolute;
    bottom: -25px;
    display: block;
    width: 100%;
    padding: 0;
    margin: 0;
    list-style: none;
    text-align: center;
    margin-left: 100px;
  }
}