.cabinet {
  width: 100%;
  min-height: 900px;
}

.lina {
  width: 1px;
  height: 100%;
  background-color: rebeccapurple;
}

.cabinet .cabinet_all_see {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
}

.cabinet .container {
  margin: 0 auto;
  max-width: 1140px;
  width: 90%;
  height: 100%;
  padding-bottom: 100px;
}

.contewainer .p {
  @media screen and (max-width:1251px) {
    width: 90%;
    margin: 0 auto;
  }
}

.cabinet .h1 {
  margin-top: 24px;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
}

.cabinet h1 {
  color: #fff;
  font-size: 16px;
  font-weight: 600;
}

.cabinet .box {
  margin: 0 auto;
  width: 100%;
  padding: 12px;
  border-bottom: 1px dotted hsla(0, 0%, 93%, 0.3);

  @media screen and (max-width:1251px) {
    padding: 0 !important;
  }
}

.cabinet .box .save {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 0.6fr 1fr 0fr;
  align-items: center;
}

.cabinet .box .save img {
  width: 45px;
  height: 45px;
  border-radius: 50%;
}

.cabinet .box .save .content {
  display: flex;
  align-items: center;
  gap: 6px;
}

.cabinet .box .save .content .h1 {
  margin: 0 0 0 10px;
  width: 100%;
  color: rgba(103, 75, 75, 0.855);
  font-size: 20px;
  font-weight: 500;
}

.cabinet .box .save .content p {
  color: #cbcbcb;
  font-size: 14px;
  font-weight: 400;
}

.cabinet .btns {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 15px;

  @media screen and (max-width:1251px) {
    grid-template-columns: 2fr;
  }
}

.cabinet .btns button {
  width: 122px;
  height: 44px;
  outline: none;
  border: 1px solid #73F4AD;
  border-radius: 10px;
  color: #fff;
  font-size: 15px;
  font-weight: 400;
  background-color: #1A1B23;
  cursor: pointer;

  @media screen and (max-width:1251px) {
    width: 94px;
    height: 35px;
  }
}

.cabinet .btns .commiss:hover {
  opacity: 0.8;
}

.cabinet .btns .conclusion {
  background-color: #1A1B23;
  border: 1.5px solid #483D6D !important;
}

.cabinet .btns .conclusion:hover {
  opacity: 0.8;
}

.cabinet .wrapper {
  width: 65%;
  position: relative;
  border-radius: 16px;
  border: 1px solid #483D6D;
  background: #1A1B23;
  overflow: hidden;
  @media screen and (max-width:1251px) {
    border: none;
    border-radius: 0;
  }
}

.cabinet .wrapper_history {
  width: 100%;
  display: flex;
  flex-direction: row;
  gap: 20px;
  margin: 20px 0 0 0;
}

.cabinet .wrapper_history .history_accaunt {
  width: 100%;
  min-height: 300px;
  padding: 20px;
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  background: #262a32;
  margin: 40px 0 0 0;
}

.cabinet .wrapper_history .boxs {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  display: grid;
  grid-template-columns: 1.2fr 1.4fr 1fr 1fr;
}

.cabinet .wrapper_history .boxs p {
  color: var(--unnamed, #9e9ea5);
  font-size: 14px;
  font-weight: 400;
}

.cabinet .wrapper_history .box_history {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  display: grid;
  grid-template-columns: 1fr 1.5fr 1fr 1fr;
  border-top: 1px solid #9e9ea564;
}

.cabinet .wrapper_history .box_history p {
  color: var(--white, #fff);
  font-size: 14px;
  font-weight: 400;
}

.cabinet .wrapper_history h2 {
  text-align: center;
}

.cabinet .wrapper_history .look {
  margin-top: 22px;
  font-size: 13px;
  font-weight: 400;
}

.cabinet .wrapper_history .wrapper_accaunt {
  width: 35%;
  height: 100%;
  display: flex;
  flex-direction: column;

  @media screen and (max-width:1251px) {
    margin: 0 auto;
    width: 90%;
  }
}



.cabinet .activeited_true,
.cabinet .activeited_not,
.cabinet .activeited_norm,
.cabinet .activeited_false {
  font-size: 16px;
  font-weight: 600;
}

.cabinet .activeited_false {
  color: #e25c5c;
}

.cabinet .activeited_true {
  color: var(--green);
}

.cabinet .activeited_norm {
  color: yellow;
}

.cabinet .activeited_not {
  color: red;
}

.cabinet .wrapper_history .wrapper_accaunt .replenishment {
  width: 100%;
  display: flex;
}

.cabinet .wrapper_history .wrapper_accaunt .replenishment p {
  width: 33%;
  color: #FCFCFC;
  font-size: 14px;
  font-weight: 400;
}

.cabinet .wrapper_history .wrapper_accaunt .replenishment.info p {
  width: 33%;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  line-height: 1;
}

.cabinet .wrapper_history .wrapper_accaunt .replenishment.info p small {
  color: rgba(255, 255, 255, .5);
  font-size: 13px;
}

.cabinet .wrapper_history .wrapper_accaunt .save_box.three {
  margin: 0;
  background-color: #1A1B23 !important;
  border: 1px solid #483D6D;
  margin-top: 20px;
  padding: 21px;
  border-radius: 16px;
}

.cabinet .wrapper_history .content_three {
  line-height: 2;
}

.cabinet .content_box {
  display: flex;
  align-items: center;
  line-height: 1.4;
  margin-bottom: 12px;
}

.cabinet .content_box div {
  margin-left: 10px;
}

.cabinet .wrapper_history .content_three .text {
  color: #c0c1c9;
  font-size: 14px;
  font-weight: 700;
  width: 144px;
}

.cabinet .wrapper_history .content_three p {
  color: #fff;
  font-size: 14px;
  font-weight: 500;
}

.cabinet .wrapper_history .content_box .red {
  color: #e25c5c;
  font-size: 16px;
  font-weight: 600;
}

.cabinet .loading_div {
  width: 100%;
  height: 600px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.cabinet .save_box_two {
  padding: 21px 21px 21px 21px;
  border-radius: 16px;
  border: 1px solid #483D6D;
  background-color: #1A1B23 !important;
}

@media (max-width: 1265px) {
  .cabinet .wrapper_history {
    display: flex;
    flex-direction: column-reverse;
    display: grid;
    grid-template-columns: 1fr;
  }
}

@media (max-width: 600px) {
  .cabinet .wrapper_history .save_box.three .content_three {
    display: grid;
    grid-template-columns: 1fr;
  }

  .cabinet .padd {
    padding: 0 10px 0 10px;
  }

  .cabinet .box .save .content {
    display: flex;
    align-items: center;
  }

  .cabinet .wrapper.slider .box {
    margin: 30px auto;
    cursor: pointer;
  }

  .cabinet .wrapper_history .history_accaunt p {
    font-size: 10px;
  }

  .cabinet .wrapper_history .history_accaunt .boxs,
  .cabinet .wrapper_history .history_accaunt .box_history {
    height: 40px;
  }

  .cabinet .content_three .content_box h3 {
    font-size: 13px;
  }

  .cabinet .content_three .content_box p {
    font-size: 13px;
  }
}