.About {
  background: url("../../img/Eclips.svg");
  background-position: top right;
  background-repeat: no-repeat;
  padding-bottom: 60px;
  background-size: auto;
}

.About .container-custom {
  margin: 0 auto;
  max-width: 1140px;
  width: 90%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.qwr-code {
  display: flex;
  justify-content: space-between;
  align-items: center;
}


.qwr-code p {
  width: 73%;
  font-size: 16px;
  font-weight: 300;
  color: #898CA9 !important;
  line-height: 1;

}

@media(max-width:800px) {
  .qwr-code p {
    margin-top: 20px;
    width: 100%;
    text-align: center;
  }

  .qwr-code {
    display: flex;
    flex-direction: column;
  }
}