#sell_cryptocurrency .container {
  max-width: 1140px;
  width: 90%;
  margin: 0 auto;
  padding-bottom: 100px;
}

#sell_cryptocurrency .holder {
  margin: 20px 0 10px 0;
}

.line_chart {
  position: absolute;
}

#sell_cryptocurrency .holder h1 {
  color: #ffffff;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin-top: 20px;
}

#sell_cryptocurrency .search_i {
  color: white;
  font-size: 20px;
  position: absolute;
  margin: 14px;
}

.width .line {
  width: 80%;
  height: 1px;
  background-color: #2d2e3c;
}

#sell_cryptocurrency .width input {
  width: 59%;
  height: 50px;
  border-radius: 10px;
  color: #ffffff;
  padding: 0 0 0 50px;
  border: none;
  outline: none;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 189.5%;
  position: relative;
  background: transparent;
}

#sell_cryptocurrency .border-class {
  border: 1px solid #483d6d;
  border-radius: 16px;
  width: 80%;
  background-color: #1a1b23;

  @media screen and (max-width: 1251px) {
    width: 100%;
    border-radius: 0;
    border: none;
    overflow-x: scroll !important;

    @media screen and (max-width:560px) {
      max-width: 600px;
    }
  }
}

.sell_cryptocurrency.custom {
  display: flex !important;
  justify-content: space-between !important;
}

#sell_cryptocurrency .buy_cryp {
  border: 1px solid rgba(255, 255, 255, 0.1);
  padding: 10px;
}

#sell_cryptocurrency .мarket {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 50px 0 20px 0;
}

#sell_cryptocurrency .мarket h3 {
  color: #9e9ea5;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 140%;
  margin: 0 27px 0 19px;
}

#sell_cryptocurrency .buy_cryp {
  display: flex;
  justify-content: center;
  align-items: center;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 0fr;
}

#sell_cryptocurrency .buy_cryp .big {
  display: flex;
  align-items: center;
}

#buy_cryptocurrency .num {
  margin-left: 28px;
}

#sell_cryptocurrency .buy_cryp .big img {
  margin-right: 10px;
  width: 45px;
  height: 45px;
}

#sell_cryptocurrency .buy_cryp h3 {
  color: #fff;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
}

.мarket_h6 {
  font-size: 18px;
  font-weight: 600;
  line-height: 24.55px;
  position: relative;
  left: 15px;

  @media screen and (max-width: 1251px) {
    font-size: 14px;
    left: 20px;
    font-weight: 700;
  }
}

#sell_cryptocurrency .buy_cryp .num {
  color: #fff;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 140%;
  padding-left: 30px;
}

#sell_cryptocurrency .buy_cryp .big h6 {
  color: #898ca9;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 140%;
}

#sell_cryptocurrency .buy_cryp button {
  margin-left: auto;
  width: 132px;
  height: 41px;
  border: none;
  border-radius: 10px;
  background: #8d6ae2;
  color: #fff;
  font-size: 15px;
  font-weight: 400;
  cursor: pointer;

  @media screen and (max-width: 1251px) {
    width: 94px;
    height: 35px;
  }
}

#sell_cryptocurrency .loading_div {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #0000008e;
}

@media (max-width: 1000px) {

  /* #sell_cryptocurrency .buy_cryp {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0;
  } */
  #sell_cryptocurrency .none_stroke,
  #sell_cryptocurrency .line_chart,
  #sell_cryptocurrency .num {
    display: none;
  }
}

@media (max-width: 700px) {
  #sell_cryptocurrency .container {
    width: 100%;
  }

  #sell_cryptocurrency .holder h1 {
    margin-bottom: 20px;
  }

  #sell_cryptocurrency .buy_cryp .big img {
    display: none;
  }

  #sell_cryptocurrency .buy_cryp .big {
    text-align: start;
  }

  #sell_cryptocurrency .holder input {
    width: 100%;
  }

  #sell_cryptocurrency .width {
    width: 100%;
  }
}