.top_up {
  width: 100%;
  min-height: 70vh;
}

.top_up .container {
  margin: 0 auto;
  max-width: 1140px;
  width: 100%;
  margin-top: 20px;
  padding-right: 76px;
}

.top_up .container .top_up_header {
  width: 100%;
  min-height: 40px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 30px;
}

.top_up .container .top_up_header h1 {
  color: var(--white, #fff);
  font-size: 23px;
  font-weight: 600;
  @media screen and (max-width:1251px) {
    margin: 0 auto;
  }
}

.top_up .top_up_header .menu_header {
  display: flex;
}

.top_up .top_up_header .menu_header .nav {
  color: var(--white, #fff);
  font-size: 16px;
  font-weight: 400;
  padding: 8px 20px;
  border-radius: 3px;
  cursor: pointer;
  outline: none;
  border: none;
}

.top_up .wrapper {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(130px, 1fr));
  gap: 24px;
  @media screen and (max-width:433px) {
    grid-template-columns: 10fr 10fr;
  }
}

