.top_up .wrapper .box {
  width: 100%;
  height: 165px;
  padding: 16px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  border-radius:25px;
  background: #262a32;
  cursor: pointer;
  transition: 0.1s ease-in-out;
}

 .fiat-top{
  @media screen and (max-width:633px) {
    margin-top: 24px;
    grid-template-columns: repeat(auto-fit, minmax(160px, 1fr))!important;
  }
}

.top_up .buttons_category p{
   color: #fff;
}
.top_up .top-line{
  width: 90%;
  height: 2px;
  background-color: rgb(115, 244, 173);
  border-radius: 4px;
  display: none;
  transition: 0.2s ease;
}
.top_up .buttons_category {
  margin-top: 30px;
  width: 100%;
  display: flex;
  @media screen and (max-width:1251px) {
    overflow-x: scroll;
  }
}
.top_up .wrapper .box:hover {
  background: #4d535e;
}
