.top_up .li{
  width: 100%;
  background-color: #4F4B5C;
  height: 1px;
}
.top_up .top-line{
  width: 90%;
  height: 2px;
  background-color: rgb(115, 244, 173);
  border-radius: 4px;
  display: none;
  transition: 0.2s ease;
}
.top_up .btn:hover + .top-line {
  display: block; 
  transform: scaleX(1);
  transform-origin: center;
  transition: transform 0.3s;
}
.top_up .active + .top-line{
  display: block; 
  transform: scaleX(1);
  transform-origin: center;
  transition: transform 0.3s;
}
.top_up .btn {
  margin-right: 20px;
  outline: none;
  border: none;
  cursor: pointer;
  color: #fff;
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 15px;
}
.top_up .btn::before{
  content: "";
  width: 100%;
  height: 1px;
  background-color: rgb(115, 244, 173);
  position: absolute;
  left: 0;
  bottom: 0;
  transform: scaleX(0);
  transform-origin: center;
  transition: transform 0.5s;
}
.top_up .wrapper .box img {
  width: 72px;
  height: 72px;
}

.top_up .wrapper .box h1 {
  color: #fff;
  font-size: 18px;
  font-weight: 600;
  padding: 6px 0 8px;
}

.top_up .wrapper .box p {
  color: #9b9b9b;
  font-size: 16px;
  font-weight: 300;
  line-height: 1;
}

.top_up .loading_div {
  width: 100%;
  height: 600px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.top_up .modal {
  top: 0;
  left: 0;
  position: fixed;
  width: 100%;
  height: 100vh;
  background: rgb(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  animation: name 0.5s;
}

@keyframes name {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.top_up .modal .not {
  width: 100%;
  height: 100vh;
  position: relative;
}

.top_up .modal .modal_container {
  position: absolute;
  max-width: 500px;
  width: 80%;
  min-height: 200px;
  padding: 20px;
  border-radius: 20px;
  background: #262a32;
}

.top_up .modal h1 {
  color: var(--white, #fff);
  font-size: 23px;
  font-weight: 500;
  text-align: center;
  line-height: 1;
}

.top_up .modal p {
  margin-top: 15px;
  color: var(--white, #fff);
  font-size: 16px;
  font-weight: 300;
}

.top_up .modal a {
  color: #4ac49e;
}

.top_up .modal img {
  margin: 0 auto;
  display: flex;
  margin-top: 15px;
  width: 200px;
}

.top_up .modal .address {
  display: block;
  margin-top: 20px;
  color: var(--white, #fff);
  font-size: 16px;
  font-weight: 500;
  line-height: 1;
  margin-bottom: 0;
}

.top_up .modal input {
  margin-top: 10px;
  width: 100%;
  outline: none;
  border: none;
  border-radius: 7px;
  background: #1d2026;
  padding: 15px;
  color: var(--unnamed, #9e9ea5);
  font-size: 16px;
  font-weight: 500;
}

.top_up .modal button {
  margin-top: 20px;
  width: 100%;
  outline: none;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  background: #4ac49e;
  padding: 15px;
  color: var(--white, #fff);
  font-size: 16px;
  font-weight: 600;
}

.top_up .modal button:hover {
  opacity: 0.8;
}

.top_up .modal_form {
  position: absolute;
  max-width: 500px;
  width: 80%;
  min-height: 200px;
  padding: 20px;
  border-radius: 20px;
  background: #262a32;
}

.top_up .modal_form input {
  color: #fff;
}

.top_up .modal_text {
  position: absolute;
  max-width: 500px;
  width: 80%;
  min-height: 100px;
  padding: 30px 20px;
  border-radius: 20px;
  background: #262a32;
}

.top_up .modal_text a {
  color: #4ac49e;
}

.top_up .modal_text img {
  max-width: 100% !important;
  max-height: 100% !important;
}

.top_up .modal_text p {
  text-align: start;
}

.top_up .box_form {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 15px;
  border-radius: 5px;
  background: #1d2026;
  margin-top: 6px;
}

.top_up .box_form p {
  margin: 0;
}

.top_up .box_form .form {
  text-align: end;
  font-weight: 500;
}

.top_up .save_form {
  margin-top: 25px;
}

.deposit-qr-image {
  border-radius: 20px;
}

@media (max-width: 750px) {
  .top_up .menu_header {
    margin: 20px 0;
    overflow-y: hidden;
  }

  .top_up .menu_header::-webkit-scrollbar {
    width: 0;
    background: none;
  }

  .top_up .menu_header::-webkit-scrollbar-thumb {
    border-radius: none;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background: none;
  }

  .top_up .modal .modal_container h1 {
    font-size: 18px;
  }

  .top_up .modal .modal_container p {
    font-size: 12px;
  }

  .top_up .modal .modal_container img {
    width: 150px;
  }

  .top_up .modal input {
    font-size: 14px;
  }

  .top_up .modal button {
    padding: 10px;
  }
}

