.head {
  width: 100%;
  min-height: 60px;
  border-right: 1px solid #4f4b5c;
}

.head .color {
  width: 87px;
  height: 32px;
  cursor: pointer;
}

.head .container {
  /* margin: 0 auto;
  max-width: 1140px;
  width: 90%; */
  height: 100%;
  position: relative;
}

.head .wrapper_balanse {
  width: 100%;
  /* display: flex; */
  /* align-items: center; */
  justify-content: space-between;
  flex-direction: column;
  margin: 20px 0;
}

.head .activeited_true,
.head .activeited_not,
.head .activeited_norm,
.head .activeited_false {
  font-size: 12px;
  font-weight: 400;
}

.head .activeited_false {
  color: #e25c5c;
}

.head .activeited_true {
  color: var(--green);
}

.head .activeited_norm {
  color: yellow;
}

.head .activeited_not {
  color: red;
}

.head .save_box.one h1 {
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  line-height: 1;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 165px;
}

.head .saves__Lider {
  border-bottom: 1px solid #4f4b5c;

  @media screen and (max-width: 1251px) {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 90%;
    margin: 0 auto;
    margin-bottom: 16px;
  }
}

.head .save_box.two h1 {
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  padding-bottom: 15px;
}

.head .save_box_user {
  width: 100%;
  display: flex;
  align-items: center;
  margin-top: 20px;
}

.head .save_box_user img {
  width: 50px;
  height: 50px;
  margin-right: 15px;
  border-radius: 50%;
  object-fit: cover;
  object-position: top;
}

.head .wrapper_balanse .balanse {
  width: 150px;
  height: 40px;
  display: flex;
  align-items: center;
}

.head .wrapper_balanse .circle {
  width: 45px;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fff;
  border-radius: 50%;
  margin-right: 12px;
}

.head .wrapper_balanse .balanse h3 {
  display: flex;
  align-items: center;
  gap: 3px;
  color: var(--base-0, #fff);
  font-size: 18px;
  font-weight: 500;
  line-height: 1;
}

.head .wrapper_balanse .balanse h3 span {
  color: #898ca9;
  font-size: 13px;
  font-weight: 600;
  line-height: 1;
}

.head .wrapper_balanse .balanse h3 small {
  color: rgba(255, 255, 255, 0.3);
  font-size: 13px;
  font-weight: 400;
  line-height: 1;
}

.head .user {
  display: flex;
  align-items: center;
  width: 100%;
  border-top: 1px solid #4f4b5c;
  border-bottom: 1px solid #4f4b5c;
}

.head .user .notification {
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background: rgba(39, 41, 56, 0.43);
  cursor: pointer;
}

.head .user img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
  object-position: top;
  margin-left: 10px;
}

.head .user h1 {
  color: var(--neutral-white, #fff);
  font-size: 16px;
  font-weight: 400;
  margin-left: 10px;
  display: flex;
}

.head .balanse .balanseFlex {
  display: flex;
  align-items: center;
  gap: 5px;
  margin-top: 20px;
  margin-bottom: 10px;

  @media screen and (max-width: 700px) {
    display: block;
    margin-top: 0;
  }
}

.head .wrapper_menu {
  padding-right: 10px;
  width: 100%;
  min-height: 50px;
  gap: 6px;
  display: grid;
  grid-template-columns: 1fr;
  background: rgba(23, 23, 28, 0.2);

  @media screen and (max-width: 1251px) {
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    overflow-x: scroll;
    gap: 21px;
    margin: 0 auto;
    min-height: 30px;
    padding: 15px 0;
    width: 90%;
  }
}

.wrapper_menu_Border {
  @media screen and (max-width:1251px) {
    width: 100%;
    height: 1px;
    background: #4f4b5c;
  }
}

@media screen and (max-width: 1251px) {
  .head .wrapper_menu::-webkit-scrollbar {
    display: none;
  }
}

.head .menu_router {
  padding: 15px 0 0 0;
  background: #00000000;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  border-radius: 3px;
  text-decoration: none;
  white-space: nowrap;
}

.head .menu_router:hover {
  color: var(--blue);
}

.head .menu_router.active {
  color: var(--blue);
}

.head .icon {
  margin-right: 10px;
}

.head .hamburger {
  cursor: pointer;
}

.head .hamburger div {
  width: 35px;
  height: 5px;
  margin: 4px 0;
  border-radius: 3px;
}

.head .hamburger.none {
  display: none;
}

.head .menu_modal {
  top: 0;
  left: 0;
  position: fixed;
  width: 100%;
  height: 100vh;
  z-index: 1;
}

.head .menu {
  top: 145px;
  right: 0;
  position: absolute;
  /* width: 200px; */
  min-height: 200px;
  padding: 10px;
  border-radius: 3px;
  background: #171822;
  /* box-shadow: 5px 10px 15px #171822; */
  z-index: 99999;
  animation: fixed_modal 0.7s ease;
}

@keyframes fixed_modal {
  0% {
    top: 80%;
    opacity: 0;
    transform: scale3d(0);
  }

  100% {
    top: 100%;
    opacity: 1;
    transform: scale3d(1);
  }
}

.head .menu .menu_router {
  margin-bottom: 5px;
}

.head .user.none {
  display: none;
}

@media (max-width: 800px) {
  .head .menu_router {
    width: 100%;
  }

  .head .wrapper_balanse .user {
    display: none;
  }

  .head .hamburger.none {
    display: block;
  }

  .head .user.none {
    width: 100%;
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }

  .head .menu {
    padding: 20px 10px;
    position: fixed;
    max-width: 300px;
    width: 80%;
    height: 100vh;
    box-shadow: 5px 0px 25px #000;
    border-top-left-radius: 15px;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    top: 0;
    right: 0;
    animation: fixed_modal_menu 0.7s ease;
  }

  @keyframes fixed_modal_menu {
    0% {
      opacity: 0;
      right: -200px;
    }

    100% {
      opacity: 1;
      right: 0;
    }
  }
}

.burgers {
  cursor: pointer;
}

.burgers input[type="checkbox"] {
  display: none;
}

.burgers div {
  width: 30px;
  height: 3px;
  border-radius: 5px;
  background-color: var(--green);
  margin: 0 0 5px 0;
  transition: transform 0.3s ease;
}

.burgers input[type="checkbox"]:checked~div:nth-child(1) {
  transform: rotate(-45deg) translate(-5px, 6px);
}

.burgers input[type="checkbox"]:checked~div:nth-child(2) {
  transform: rotate(45deg) translate(-5px, -6px);
}

.burgers input[type="checkbox"]:checked~div:nth-child(3) {
  transform: scale(0);
}

.balance-text {
  color: #fcfcfc;
  line-height: 1;
  white-space: nowrap;
  font-size: 18px;
  font-weight: 600;
}