.operations {
  width: 100%;
  min-height: 600px;
}

.operations .container {
  margin: 0 auto;
  max-width: 1140px;
  width: 90%;
  padding-bottom: 100px;

  @media screen and (max-width: 1100px) {
    width: 100%;
  }
}

.operations h1 {
  color: var(--white, #fff);
  font-size: 18px;
  font-weight: 600;
  margin: 8px 0 17px;

  @media screen and (max-width: 1100px) {
    display: none;
    margin: 0;
  }
}

.operations .wrapper {
  width: 100%;
  background-color: #1d2026;
  border-radius: 20px;
  border: 1px solid #483d6d;
  overflow: hidden;
  @media screen and (max-width: 1100px) {
    border-radius: 0;
    margin-top: 0;
  }
}

.operations .wrapper_mobile {
  width: 100%;
  background-color: #1d2026;
  border-radius: 20px;
  border: 1px solid #483d6d;

  @media screen and (max-width: 1100px) {
    border-radius: 0;
    margin-top: 0;
  }
}

.save__lider {
  display: grid !important;
  grid-template-columns: 0.4fr 1fr 1fr 1.2fr 0.4fr 0.9fr !important;
  padding-left: 40px;

  @media screen and (max-width: 1100px) {
    display: grid !important;
    align-items: center;
  }
}

.save__lider_mobile {}

.border__save {
  width: 100%;
  height: 1px;
  background: #898ca9;
}

.operations .wrapper .save_oper,
.operations .wrapper .box {
  width: 100%;
  display: grid;
  gap: 20px;
  grid-template-columns: 1fr 1fr 1.6fr 1fr 1fr 1fr;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 10px;
}

.operations .wrapper .save_oper,
.operations .wrapper_mobile .box_mobile {
  border-bottom: 1px solid #4f4b5c;
}

.operations .wrapper .save_oper:last-child,
.operations .wrapper_mobile .box_mobile:last-child {
  border-bottom: none;
}

.box__flex_ {
  display: grid;
  align-items: center;
  grid-template-columns: 1fr 1fr;
  gap: 35%;
  padding-right: 60px;
  padding-left: 20px;

  @media screen and (max-width: 1100px) {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 0 !important;
  }
}

.operations .wrapper .save_oper img {
  width: 32px;
  height: 32px;
}

.operations .wrapper_mobile .save_oper img {
  width: 32px;
  height: 32px;
}

.operations .wrapper .box p {
  color: var(--unnamed, #fcfcfc);
  font-size: 16px;
  font-weight: 500;
  text-align: center;

  @media screen and (max-width: 700px) {
    font-size: 14px;
    font-weight: 700;
  }
}

.operations .wrapper_mobile .box_mobile p {
  color: var(--unnamed, #fcfcfc);
  font-size: 18px;
  font-weight: 600;
  text-align: center;

  @media screen and (max-width: 700px) {
    font-size: 14px;
    font-weight: 700;
  }
}

.operations .logo_mobile {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.operations .wrapper .save_oper p {
  display: block;
  color: #fff;
  font-size: 16px;
  font-weight: 400;
  text-align: center;
  padding: 0;
}

.operations .wrapper_mobile .save_oper p {
  display: block;
  color: #fff;
  font-size: 16px;
  font-weight: 400;
  text-align: center;
  padding: 0;
}

.operations .wrapper .save_oper .name p {
  font-size: 14px;
  font-weight: 600;
  line-height: 1;
}

.operations .wrapper_mobile .save_oper .name p {
  font-size: 14px;
  font-weight: 600;
}

.operations .wrapper .save_oper .btn {
  margin-left: auto;
  min-width: 150px;
  padding: 12px 0;
  outline: none;
  border: none;
  cursor: pointer;
  color: #fff;
  font-size: 15px;
  font-weight: 400;
  border-radius: 5px;
}

.operations .modal_home {
  top: 0;
  left: 0;
  position: fixed;
  width: 100%;
  height: 100%;
  background: #00000081;
  display: flex;
  align-items: center;
  justify-content: center;
}

.operations .modal {
  position: absolute;
  max-width: 430px;
  width: 80%;
  min-height: 300px;
  border-radius: 20px;
  background: #262a32;
  padding: 20px;
  overflow-x: hidden;
}

.operations .modal h1 {
  text-align: center;
}

.operations .modal .type {
  margin-top: 15px;
  border-radius: 5px;
  margin-bottom: 15px;
}

.operations .modal .type_box {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px 15px;
  border-radius: 5px;
  background: #1d2026;
  margin-bottom: 5px;
}

.operations .modal p {
  color: #fff;
  font-size: 16px;
  font-weight: 300;
  margin: 5px 0;
}

.operations .modal .type p {
  font-size: 15px;
}

.operations .modal h2 {
  color: var(--unnamed, #4ac49e);
  font-size: 16px;
  font-weight: 600;
}

.operations .modal .type_box p {
  margin: 0;
}

.operations .modal .type_box h3 {
  color: #fff;
  font-size: 16px;
  font-weight: 500;
}

.operations .modal .input_box {
  position: relative;
  margin-bottom: 15px;
}

.operations .modal .input_box input {
  width: 100%;
  padding: 12px;
  outline: none;
  border: none;
  border-radius: 5px;
  background: #1d2026;
  font-size: 15px;
  color: #fff;
}

.operations .input_box .icon {
  top: 8px;
  right: 8px;
  position: absolute;
  color: var(--green);
  cursor: pointer;
}

.operations .modal .close {
  margin: 0 auto;
  margin-top: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  outline: none;
  border: none;
  cursor: pointer;
  padding: 12px 35px;
  border-radius: 5px;
  background: #4ac49e;
  color: var(--white, #fff);
  font-size: 16px;
  font-weight: 500;
}

.operations .not {
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  height: 100vh;
}

.operations .modal .close:hover {
  opacity: 0.8;
}

.operations .name p {
  display: flex;
  text-align: start;
  padding-left: 60px;
}

.operations .name {
  display: flex;
  flex-direction: column;
}

.operations .loading_div {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.operations .not_data {
  width: 100%;
  height: 300px;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.operations .not_data .not_data_icon {
  color: var(--green);
  margin-bottom: 20px;
}

.operations .not_data .not_data_h1 {
  text-align: center;
  font-size: 18px !important;
  font-weight: 400 !important;
}

.operations .wrapper .save_oper.cross {
  display: none;
}

.opt-time {
  display: block;
  color: rgba(255, 255, 255, 0.6);
  font-size: 14px;
  text-align: center;
}

.operations .wrapper_mobile {
  display: none;
}

.operations .box_mobile p {
  color: var(--unnamed, #fcfcfc);
  font-size: 18px;
  font-weight: 600;
  text-align: center;

  @media screen and (max-width: 700px) {
    font-size: 14px;
    font-weight: 700;
  }
}

.operations .save__lider_mobile {
  display: none !important;
}

@media (max-width:500px) {
  .operations .save__lider_mobile {
    display: block !important;
    display: grid !important;
    grid-template-columns: 1fr 1fr 1fr 1.2fr !important;
    padding: 10px 0 !important;
    align-items: center;
    gap: 20px;
  }

  .opt-time {
    display: none;
  }

  .operations .name {
    display: flex !important;
    flex-direction: column !important;
  }

}

@media (max-width: 700px) {
  .operations .wrapper_mobile {
    display: block;
  }

  .operations .save__lider {
    display: none !important;
  }

  .operations .name {
    display: flex !important;
    flex-direction: row;
    gap: 10px;
  }

  .operations .wrapper .save_oper,
  .operations .wrapper_mobile .box_mobile {
    display: block;
    width: 100%;
    display: grid;
    gap: 20px;
    grid-template-columns: 1fr 1fr 1.6fr 1fr;
    align-items: center;
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .cross {
    display: none;
  }

  .operations .wrapper .save_oper img {
    display: none;
  }

  .operations .wrapper {
    display: none;
  }

  .operations .wrapper .save_oper p {
    color: #fff;
    font-size: 14px;
    font-weight: 600;
  }

  .operations .wrapper .save_oper .summ {
    font-size: 18px;
    font-weight: 500;
    margin-left: auto;
  }

  .operations .save__lider_mobile {
    display: block !important;
    display: grid !important;
    grid-template-columns: 1fr 1fr 1.6fr 1fr;
    padding: 20px 0;
    align-items: center;
    gap: 20px;
  }

  .operations .wrapper_mobile .save_oper p {
    font-size: 14px;
    font-weight: 300;
  }

}