.register {
  width: 100%;
  min-height: 200px;
  background: url("../../img/Eclipse.svg") no-repeat center / cover;
}

.register .container {
  margin: 0 auto;
  max-width: 1140px;
  width: 90%;
  padding-top: 180px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  padding-bottom: 100px;
}

.register h1 {
  color: #fff;
  font-size: 30px;
  font-weight: 600;
  margin-bottom: 10px;
}

.register .agree {
  color: #fff;
  font-size: 14px;
  font-weight: 400;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  gap: 5px;
}

.register a {
  font-size: 14px;
  font-weight: 500;
}

.register .save {
  margin-top: 25px;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  margin-bottom: 30px;
}

.register .bg_not {
  width: 100%;
}

.register label {
  color: #fff;
  font-size: 15px;
  font-weight: 400;
}

.register .top {
  margin-top: 20px;
}

.register input {
  margin-top: 11px;
  width: 100%;
  outline: none;
  background: #171822;
  border: 1.2px solid #9e9ea5;
  border-radius: 10px;
  padding: 16px;
  font-size: 14px;
  font-weight: 700;
  color: #fff;
  position: relative;
  transition: 0.2s ease;
}

.register input:hover {
  border: 1.2px solid #fff;
}

.register input::placeholder {
  color: #9e9ea5;
  font-size: 14px;
  font-weight: 700;
}

.register .check {
  margin-top: 20px;
  width: 100%;
}

.wrapper h3 {
  color: #fff;
  font-weight: 300;
}

.register .check p {
  color: #898ca9;
  font-size: 14px;
  font-weight: 300;
  text-align: center;
  color: #fff;
}

.register .btn {
  display: flex;
  justify-content: center;
  margin: 25px auto;
  width: 25%;
  padding: 14px;
  outline: none;
  border: none;
  cursor: pointer;
  background: #251f33 !important;
  border-radius: 15px;
  color: #fff;
  font-size: 14px;
  font-weight: 700;
}

.register .btn:hover {
  opacity: 0.8;
}

.register .span-icon {
  font-size: 20px;
  color: white;
  position: absolute;
  z-index: 1;
  margin: 25px 0 0 -40px;
  cursor: pointer;
}

.register .span-icon1 {
  font-size: 20px;
  /* top: 0; */
  /* left: 0; */
  color: white;
  position: absolute;
  z-index: 1;
  margin: 34px 0 0 -40px;
  cursor: pointer;
}

@media (max-width: 800px) {
  .register label {
    text-align: start;
  }

  .register .save {
    margin-top: 25px;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr;
    gap: 20px;
    margin-bottom: 30px;
  }

  .register .btn {
    width: 100%;
  }

  .register .container {
    padding-top: 70px;
    display: flex;
    flex-direction: column-reverse;
  }

  .register .bg_not {
    margin: 0 auto;
    width: 60%;
  }
}