.button {
  outline: none;
  border: none;
  padding: 15px 35px;
  background: var(--green);
  border: 1px solid transparent;
  border-radius: 5px;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  text-align: center;
  text-decoration: none;
  position: relative;
}

.button.button-loader:disabled {
  color: transparent;
}

.button.button-loader:disabled::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  border: 3px solid rgba(255, 255, 255, 0.6);
  border-top: 3px solid #fff;
  border-radius: 50%;
  width: 45px;
  height: 45px;
  animation: spin 1s linear infinite;
}

.button.button-loader.sm::before {
  width: 16px;
  height: 16px;
}

.button.button-loader.md::before {
  width: 24px;
  height: 24px;
}

.button.button-loader.lg::before {
  width: 32px;
  height: 32px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.button:hover {
  background-color: #4ac49d;
  cursor: pointer;
}

.button.outline {
  border: 1px solid var(--green);
  background-color: transparent;
}

.button.outline:focus {
  background-color: #4ac49d;
}

.button.block {
  width: 100%;
}

.header-nav {
  display: flex;
  gap: 20px;
  margin: 20px 0 0 0;
}

.header-nav .nav {
  background-color: transparent;
  color: var(--white, #fff);
  font-size: 16px;
  font-weight: 400;
  border-radius: 0;
  cursor: pointer;
  outline: none;
  border: none;
}

.li {
  width: 100%;
  background-color: #4f4b5c;
  height: 1px;
}

.header-nav .p:hover+.top-line {
  display: block;
  transform: scaleX(1);
  transform-origin: center;
  transition: transform 0.3s;
}

.header-nav .active+.top-line {
  display: block;
  transform: scaleX(1);
  transform-origin: center;
  transition: transform 0.3s;
  margin-top: 11px;
}

.header-nav .top-line {
  /* width: 90%; */
  height: 2px;
  background-color: rgb(115, 244, 173);
  border-radius: 4px;
  display: none;
  transition: 0.2s ease;
}

.header-nav .nav.active {
  color: var(--blue);
  padding: 10px 0;
  border-bottom: 2px solid var(--blue);
}

.modal-window {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgb(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  animation: name 0.5s;
  z-index: 111;
}

.modal-window .modal-overlay {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  position: absolute;
}

.modal-window h1 {
  color: var(--white, #fff) !important;
  font-size: 23px !important;
  font-weight: 500 !important;
  text-align: center !important;
  line-height: 1 !important;
}

.modal-window .modal-text {
  position: absolute;
  max-width: 500px;
  width: 80%;
  min-height: 100px;
  padding: 20px;
  border-radius: 20px;
  background: #262a32;
}

.modal-window .modal-text a {
  color: #4ac49e;
}

.modal-window .modal-text img {
  max-width: 100% !important;
  max-height: 100% !important;
}

.modal-window .modal-text p {
  margin-top: 15px;
  color: var(--white, #fff);
  font-size: 16px;
  font-weight: 300;
  line-height: 1.1;
}

.modal-window button {
  margin-top: 20px;
  width: 100%;
  outline: none;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  background: #4ac49e;
  padding: 15px;
  color: var(--white, #fff);
  font-size: 16px;
  font-weight: 600;
}

